










































.box-card {
    width: 100%;
    position: relative;

    /deep/ .search {
        padding: 0;
    }

    /deep/ .el-card__header {
        padding: 0.16rem 0.13rem 0.16rem 0.4rem;
        position: relative;
        font-family: 'PFC';
        font-size: 16px;
    }

    /deep/ .el-card__header:after {
        content: "";
        position: absolute;
        background: #2e4af7;
        left: 0.24rem;
        top: 0.16rem;
        width: 0.06rem;
        height: 0.2rem;
    }
}
