
































































































































































































































































.box {
    .item {
        padding-bottom: 24px;
    }

    padding-top: 24px;
    position: relative;

    .print-btn {
        position: absolute;
        right: 20px;
        top: 24px;
    }

    .cus-btn {
        color: #2E4AF7;
        font-size: 14px;
        padding-left: 25px;
        cursor: pointer;
        line-height: 22px;
        position: relative;

        &:first-child {
            padding-left: 0;
        }
    }

    .table-btns {
        text-align: center;

        .cus-btn {
            padding: 0;
        }

        .line:after {
            content: "";
            position: absolute;
            display: block;
            right: -13px;
            top: 6px;
            bottom: 0;
            height: 12px;
            width: 1px;
            background: #E3E5EC;
        }

        .center {
            width: 100%;
        }

    }
}

.form {
    .li {

        display: flex;
        margin-bottom: 22px;

        /deep/ .el-form-item {
            margin-bottom: 0;
        }

        .item {
            width: 100%;
            display: flex;
            line-height: 32px;
            height: 32;
            padding-bottom: 0;

            .label {
                font-family: "PFC";
                min-width: 0.8rem;
                margin-right: 2px;
                color: #333;
                text-align: right;
            }

            .box {
                width: 100%;
                border-radius: 4px;
                color: #666;
                padding: 0 10px;
                box-sizing: border-box;
            }

            .rt {
                flex: 1;
            }

            .btns {
                width: 100%;
                display: flex;
                justify-content: flex-end;

            }
        }
    }
}
