
















































































































































































































































































@media print {
    #table-content {
        zoom: 85%;
        margin-top: 0 !important;
    }

    .head {
        font-size: 18px;
        font-weight: 600;
    }

    .scorll {
        overflow: hidden;
        padding-right: 2px;

    }


    .desc-icon,
    .desc-input {
        display: none !important;
    }

    .desc-print {
        display: inline-block !important;
    }

    .table-h {
        color: #000;
    }

    th,
    td {
        border: .1pt solid !important;
    }

    td,
    th,
    span {
        font-size: 9pt !important;
        padding: 40px 0px !important;
    }

    th,
    td {
        border: .1pt solid !important;
    }

    td,
    span {
        color: #000;
    }

    .table-footer,
    .table-h-list,
    .table-h {
        line-height: 0.3in !important;
    }

    .left {
        margin-left: 3in !important;
    }

    .right {
        margin-right: 2in !important;
    }



    table {
        page-break-inside: avoid;
    }

}



#table-content {
    margin-top: 50px;
}



.scorll {
    overflow: auto;
    padding-bottom: 10px;
    padding-right: 2px;
}

.noBorder {
    td {
        border: 0 !important;
    }

    &:hover {
        background-color: transparent !important;
    }
}

.box {
    padding-top: 24px;
    position: relative;

    .print-btn {
        position: absolute;
        right: 0;
        top: 24px;
    }

    .desc-print {
        display: none;
    }

    .desc-input {
        border: 0;
        outline: none;
        color: #303133;
        font-size: 14px;
        font-family: "PFM" !important;
    }
}

th,
td {
    border: 1px solid #d3d3d3;
}

th,
td {
    text-align: center;
    padding: 10px 10px;
    white-space: nowrap;
}

tr:nth-child(even) {
    // background-color: #f2f2f2;
}

.table {
    border-collapse: collapse;
    width: 100%;
}

.item {
    margin-bottom: 16px;

    .table-h {
        font-size: 16px;
        text-align: center;
        line-height: 40px;

    }

    .table-h-list {
        line-height: 40px;
        display: flex;
        justify-content: space-between;
        font-size: 14px;

        span {
            text-align: left;

            &:last-child {
                text-align: right;
            }
        }
    }

    .table-footer {
        display: flex;
        justify-content: space-between;
        line-height: 40px;

        .left {
            margin-left: 150px;
        }

        .right {
            margin-right: 100px;
        }
    }
}
